import firebase from 'firebase';
import 'firebase/auth';
import 'firebase/firestore';
import { useHistory } from 'react-router-dom';

export const configFirebase = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  databaseURL: process.env.REACT_APP_FIREBASE_DATA_BASE_URL,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BACKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSEAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID,
  measurementId: process.env.REACT_APP_FIREBASE_MEASUREMENT_ID,
};

export const getFirebase = () => {
  return !firebase.apps.length
    ? firebase.initializeApp(configFirebase)
    : firebase.app();
};

const errorFirebaseAuth = {
  authwrongpassword: { input: 'password', messenge: 'błędzne hasło' },
  authtoomanyrequests: {
    input: 'password',
    messenge: 'odpocznij zbyt wiele niepoprwnch logowań zapraszamy pózniej',
  },
  authusernotfound: {
    input: 'email',
    messenge: 'jeszcze się nie znamy zapraszam do rejestracji',
  },
  authemailalreadyinuse: {
    input: 'email',
    messenge: 'już się znamy zapraszam do logowania',
  },
};

const removeMinusAndSlash = (error) => {
  return error.replace(/[^a-zA-Z0-9]+/g, '');
};

export const signInSocialMedia = (social) => {
  let provider;
  social === 'google' && (provider = new firebase.auth.GoogleAuthProvider());
  social === 'facebook' &&
    (provider = new firebase.auth.FacebookAuthProvider());
  firebase
    .auth()
    .signInWithPopup(provider)
    .then(function (result) {
      const token = result.credential;
      const user = result.user;

      social === 'google' && console.log('LoginByGoogle', token, user);
      social === 'facebook' && console.log('LoginByFacebook', token, user);
      firebase
        .auth()
        .currentUser.getIdToken(true)
        .then((idToken) => {
          console.log('idToken :>> ', idToken);
        })
        .catch((error) => {
          console.log('error :>> ', error);
        });
    })
    .catch(function (error) {
      // Handle Errors here.
      const errorCode = error.code;
      const errorMessage = error.message;
      // The email of the user's account used.
      const email = error.email;
      // The firebase.auth.AuthCredential type that was used.
      const credential = error.credential;
      // ...
      social === 'google' && console.log('Error LoginByGoogle');
      social === 'facebook' && console.log('Error LoginByFacebook');
      console.error(error);
    });
};

export const signInEmailPassword = async (values) => {
  let dataBackFromFirebase = await firebase
    .auth()
    .signInWithEmailAndPassword(values.email, values.password)
    .then((user) => {
      return user;
      // console.log('user :>> ', user);
    })
    .catch(async (err) => {
      throw errorFirebaseAuth[removeMinusAndSlash(err.code)];
    });
  return dataBackFromFirebase;
};

export const signUpEmailPassword = async (values) => {
  const dataFromFirebase = await firebase
    .auth()
    .createUserWithEmailAndPassword(values.email, values.password)
    .then((user) => {
      return user;
    })
    .catch((err) => {
      throw errorFirebaseAuth[removeMinusAndSlash(err.code)];
    });
  return dataFromFirebase;
};

export const sendEmailToResetPassword = async (email) => {
  const dataFromFirebase = await firebase
    .auth()
    .sendPasswordResetEmail(email)
    .then((data) => {
      return data;
    })
    .catch((err) => {
      console.log('err', err);
      throw errorFirebaseAuth[removeMinusAndSlash(err.code)];
    });
  return dataFromFirebase;
};

export const verificationEmail = () => {
  const user = firebase.auth().currentUser;

  user
    .sendEmailVerification()
    .then(() => {
      console.log('send email :>> ');
      // Email sent.
    })
    .catch((error) => {
      console.log('error :>> ', error);
    });
};

export const getIdToken = () => {
  firebase
    .auth()
    .currentUser.getIdToken(true)
    .then((idToken) => {
      console.log('idToken :>> ', idToken);
    })
    .catch((error) => {
      console.log('error :>> ', error);
    });
};

export const getCurrentUser = () => {
  const userCurrent = firebase.auth().currentUser;
  userCurrent
    .sendEmailVerification()
    .then(() => {
      console.log('send email :>> ');
      // Email sent.
    })
    .catch((error) => {
      console.log('error :>> ', error);
    });
  console.log('userCurrent :>> ', userCurrent);
};
